*, *::after, *::before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

@font-face {
  font-family: Plus Jakarta Sans;
  src: url(./assets/fonts/PlusJakartaSans-VariableFont_wght.ttf);
}

$mobile-width: 280px;
$desktop-width: 1024px;

@mixin mobile {
  @media (min-width: #{$mobile-width}) and (max-width: #{$desktop-width}) {
    @content;
  }
}

a {
  font-weight: 900;
  text-decoration: none;
  color: var(--Very-dark-blue);
  transition: 200ms ease-in-out;

  &:is(:hover, :focus){
    color: var(--Blue);
  }
}

:root {

    /* Primary */
    --Red: hsl(1, 90%, 64%);
    --Blue: hsl(219, 85%, 26%);

    /* Neutral */
    --White: hsl(0, 0%, 100%);
    --Very-light-grayish-blue: hsl(210, 60%, 98%);
    --Light-grayish-blue-1: hsl(211, 68%, 94%);
    --Light-grayish-blue-2: hsl(205, 33%, 90%);
    --Grayish-blue: hsl(219, 14%, 63%);
    --Dark-grayish-blue: hsl(219, 12%, 42%);
    --Very-dark-blue: hsl(224, 21%, 14%);

    --Radius-sm: .5rem;

    /* Font Size */
    --fs-2: clamp(0.69rem, calc(0.62rem + 0.36vw), 0.88rem);
    --fs-1: clamp(0.83rem, calc(0.73rem + 0.52vw), 1.10rem);
    --fs0: clamp(1.00rem, calc(0.85rem + 0.73vw), 1.38rem);
    --fs1: clamp(1.20rem, calc(1.00rem + 1.01vw), 1.72rem);
    --fs2: clamp(1.44rem, calc(1.16rem + 1.38vw), 2.15rem);
    --fs3: clamp(1.73rem, calc(1.35rem + 1.87vw), 2.69rem);
    --fs4: clamp(2.07rem, calc(1.57rem + 2.50vw), 3.36rem);
    --fs5: clamp(2.49rem, calc(1.82rem + 3.33vw), 4.20rem);
  }

  body {
    font-family: 'Plus Jakarta Sans', sans-serif;
    line-height: 1.2;
    background-color: var(--Very-light-grayish-blue);
    color: var(--very-dark-blue);
    display: grid;
    place-items: center;
    padding: 2rem;
    height: 100vh;

    @include mobile {
      width: 100%;
      height: 100vh;
      padding: 0;
    }
  }

  .container {
    background-color: var(--White);
    border-radius: var(--Radius-sm);
    box-shadow: 0 4px 2rem var(--Ligh-grayish-blue-1);
    padding: 2rem;

    @include mobile {
      width: 100%;
      padding: 1rem;
    }
  }

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    & .title {
      display: flex;
      align-items: center;
      gap: .5rem;
  
      & h1 {
        font-size: var(--fs2);
        line-height: 1;

        @include mobile {
          font-size: var(--fs1);
        }
      }
  
      & .badge {
        background-color: var(--Blue);
        color: var(--White);
        padding: 0.4rem 0.6rem;
        font-weight: 600;
        border-radius: var(--Radius-sm);

        @include mobile {
          padding: 0.2rem 0.6rem;
        }
      }
    }

    & button {
      font: inherit;
      background: none;
      border: none;
      cursor: pointer;
      color: var(--Dark-grayish-blue);
      transition: 200ms ease-in-out;
      font-weight: 400;
  
      &:is(:hover){
        font-weight: 900;
        color: var(--Blue);
      }

      @include mobile {
        font-size: var(--fs-1);
      }
    }
  }
  
  .wrapper {
    display: grid;
    padding: 2rem 0rem;
    gap: 1rem;
    align-content: space-between;
      & .notification {
      padding: 1rem 0rem;
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      color: var(--Dark-grayish-blue);
    }

    & [data-unread="true"] {
        border-radius: var(--Radius-sm);
        background-color: var(--Light-grayish-blue-1);
        padding: 1.6rem;

        @include mobile {
          padding: 1rem;
        }
    }
    & img {
      width: 4rem;

      @include mobile {
        width: 3rem;
      }
    }
  }

  .a {
    display: flex;
    flex-direction: column;
    padding: 0 1rem;
    align-self: center;
    gap: 0.8rem;
    max-width: fit-content;
    
    @include mobile{
      font-size: var(--fs-1);
    }

    & .post-text {
      display: flex;
      gap: 0.6rem;
      align-items: center;
      width: 100%;

      @include mobile {
        display: grid;
      }
    }

    & .b {
      display: flex;
      align-items: center;
      gap: .6rem;
    }

    & .privateMessage {
      border: 1px solid var(--Light-grayish-blue-2);
      padding: 2rem;
      transition: 200ms ease-in-out;
      margin-top: .6rem;
      &:is(:hover) {
        cursor: pointer;
        background-color: var(--Light-grayish-blue-2);
      }
    }
  }

  .isUnread {
    background-color: red;
    display: block;
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 100vmax;

    @include mobile {
      position: absolute;
      left: 20rem;
      margin-top: 1.4rem;
    }
  }